@import url(https://fonts.googleapis.com/css2?family=Poiret+One&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
    --largeurNavigateur: 900px;
    --largeurMobile: 350px;
}

.Pages{
    background-color: white;
    display: grid;
    grid-template-columns: calc((100% - 900px)/2) 900px calc((100% - 900px)/2);
    grid-template-columns: calc((100% - var(--largeurNavigateur))/2) var(--largeurNavigateur) calc((100% - var(--largeurNavigateur))/2);
}

.LeftColumn, .RightColumn{
    background-color: #f2f2f2;
}

#uaLogoMenu{
    height: 50px;
}

#ulMenuNavigation {
    list-style: none;
    display: grid;
    grid-template-areas: "a b c d e";
    grid-template-columns: auto 150px auto auto auto;
    grid-gap: 50px;
    justify-content: center;
    justify-items: center;
    align-items: center;
    background-color: rgba(19, 67, 91, 0);
    padding: 10px;
    position: fixed;
    width: 100%;
    height: 50px;
    top:-18px;
    left:0;
    z-index: 10;

    transition-property: background-color;
    transition-duration: 0.5s;
}

#liMenuNavigationIDlogo {
    grid-area: a;
    transition-property: opacity;
    transition-duration: 0.5s;}
#liMenuNavigationID {grid-area: c;}
#liMenuNavigationIDVieAssociative{grid-area: d;}
#liMenuNavigationIDManifestationAssociative{grid-area: e;}

.liMenuNavigation a
{
    text-decoration: none;
    color: white;
    font-family: "Poiret One", serif;
}

/* width */
::-webkit-scrollbar {
    width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}
#SplashPage-h1{
    font-family: "Poiret One", serif;
    color: white;
    text-align: center;
}

#SplashPage-Container{
    background-color: rgba(19, 67, 91, 1);
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    justify-items: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
}
